const GAMES: GameInfo[] = [
  {
    title: "T√¨m S·ªë Ti·∫øp Theo",
    color: 0x3b82f6,
    posX: -25,
    hue: 0.6,
    emoji: "üî¢",
    description: "T√¨m quy lu·∫≠t v√† ch·ªçn s·ªë ti·∫øp theo trong d√£y s·ªë",
    difficulty: "‚≠ê D·ªÖ - Trung B√¨nh",
    gameType: GAME_TYPES.NEXT,
  },
  {
    title: "S·ªë C√≤n Thi·∫øu",
    color: 0xf97316,
    posX: 0,
    hue: 0.1,
    emoji: "‚ùì",
    description: "T√¨m s·ªë b·ªã thi·∫øu trong d√£y s·ªë ƒë·∫ßu ti√™n",
    difficulty: "‚≠ê‚≠ê Trung B√¨nh",
    gameType: GAME_TYPES.MISSING,
  },
  {
    title: "S·∫Øp X·∫øp D√£y S·ªë",
    color: 0xa855f7,
    posX: 25,
    hue: 0.8,
    emoji: "üìä",
    description: "S·∫Øp x·∫øp c√°c s·ªë theo quy lu·∫≠t tƒÉng d·∫ßn",
    difficulty: "‚≠ê‚≠ê‚≠ê Kh√≥",
    gameType: GAME_TYPES.SORT,
  },
]

export default function GameMenu() {
  const router = useRouter()
  useEffect(() => {
    const container = document.getElementById("menu-canvas-container")
    if (!container) return

    const scene = new THREE.Scene()
    const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000)
    const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true })

    renderer.setSize(window.innerWidth, window.innerHeight)
    renderer.setClearColor(0x000000, 0)
    renderer.outputColorSpace = THREE.SRGBColorSpace
    renderer.shadowMap.enabled = true
    renderer.shadowMap.type = THREE.PCFSoftShadowMap

    container.appendChild(renderer.domElement)
    camera.position.z = 50

    const spheres: THREE.Group[] = []
    const particles: THREE.Mesh[] = []

    GAMES.forEach((game, idx) => {
      const group = new THREE.Group()

      const geometry = new THREE.IcosahedronGeometry(2.5, 4)
      const material = new THREE.MeshStandardMaterial({
        color: game.color,
        metalness: 0.9,
        roughness: 0.05,
        emissive: game.color,
        emissiveIntensity: 1.0,
        envMapIntensity: 1.5,
      })
      const sphere = new THREE.Mesh(geometry, material)
      sphere.castShadow = true
      group.add(sphere)

      for (let i = 0; i < 4; i++) {
        const glowGeometry = new THREE.IcosahedronGeometry(2.5 + i * 0.25, 3)
        const glowMaterial = new THREE.MeshStandardMaterial({
          color: game.color,
          emissive: game.color,
          emissiveIntensity: 0.6,
          metalness: 0.5,
          roughness: 0.3,
          transparent: true,
          opacity: (0.4 - i * 0.08),
          side: THREE.BackSide,
        })
        const glow = new THREE.Mesh(glowGeometry, glowMaterial)
        group.add(glow)
      }

      group.position.x = game.posX
      group.position.y = 0
      group.userData = { originalY: 0, speed: 0.001, index: idx }
      scene.add(group)
      spheres.push(group)

      const particleCount = 12
      for (let i = 0; i < particleCount; i++) {
        const pGeometry = new THREE.SphereGeometry(0.28, 24, 24)
        const pMaterial = new THREE.MeshStandardMaterial({
          color: game.color,
          metalness: 0.9,
          roughness: 0.05,
          emissive: game.color,
          emissiveIntensity: 1.0,
          envMapIntensity: 1.5,
          transparent: true,
          opacity: 0.95,
        })
        const particle = new THREE.Mesh(pGeometry, pMaterial)
        particle.castShadow = true
        const angle = (i / particleCount) * Math.PI * 2
        particle.userData = {
          parentIndex: idx,
          angle: angle,
          distance: 5.5,
          speed: 0.025,
          index: i,
        }
        scene.add(particle)
        particles.push(particle)
      }
    })

    const light1 = new THREE.PointLight(0x00d9ff, 2, 100)
    light1.position.set(40, 40, 40)
    scene.add(light1)

    const light2 = new THREE.PointLight(0xff006e, 2, 100)
    light2.position.set(-40, -40, 40)
    scene.add(light2)

    const light3 = new THREE.PointLight(0xfdb813, 1.5, 80)
    light3.position.set(0, 50, 0)
    scene.add(light3)

    const ambientLight = new THREE.AmbientLight(0xffffff, 0.6)
    scene.add(ambientLight)

    const animate = () => {
      requestAnimationFrame(animate)

      const time = Date.now() * 0.001

      spheres.forEach((sphere) => {
        sphere.rotation.x += 0.003
        sphere.rotation.y += 0.005
        sphere.rotation.z += 0.002

        sphere.position.y = sphere.userData.originalY + Math.sin(time * 0.8 + sphere.userData.index) * 1.8

        const scale = 1 + Math.sin(time * 1.5 + sphere.userData.index * 1.5) * 0.2
        sphere.scale.set(scale, scale, scale)

        sphere.children.forEach((child, i) => {
          if (i > 0) {
            child.rotation.x += 0.001 * (i + 1)
            child.rotation.y += 0.002 * (i + 1)
          }
        })
      })

      particles.forEach((particle) => {
        if (particle.userData.parentIndex !== undefined) {
          const parentSphere = spheres[particle.userData.parentIndex]
          particle.userData.angle += particle.userData.speed

          particle.position.x = parentSphere.position.x + Math.cos(particle.userData.angle) * particle.userData.distance
          particle.position.y = parentSphere.position.y + Math.sin(particle.userData.angle * 0.6) * particle.userData.distance
          particle.position.z = Math.sin(particle.userData.angle * 1.3) * particle.userData.distance * 0.6

          particle.rotation.x += 0.04
          particle.rotation.y += 0.04
          particle.rotation.z += 0.02

          const pulse = Math.sin(time * 2.5 + particle.userData.parentIndex + particle.userData.index * 0.1) * 0.5 + 0.5
          if (particle.material instanceof THREE.MeshStandardMaterial) {
            particle.material.emissiveIntensity = pulse * 1.0 + 0.3
          }

          const particleScale = Math.sin(time * 3 + particle.userData.index) * 0.3 + 0.7
          particle.scale.set(particleScale, particleScale, particleScale)
        }
      })

      renderer.render(scene, camera)
    }

    animate()

    const handleResize = () => {
      camera.aspect = window.innerWidth / window.innerHeight
      camera.updateProjectionMatrix()
      renderer.setSize(window.innerWidth, window.innerHeight)
    }

    window.addEventListener("resize", handleResize)

    return () => {
      window.removeEventListener("resize", handleResize)

      spheres.forEach((sphere) => {
        sphere.traverse((child: THREE.Object3D) => {
          if (child instanceof THREE.Mesh) {
            child.geometry.dispose()
            if (Array.isArray(child.material)) {
              child.material.forEach((material: THREE.Material) => material.dispose())
            } else {
              child.material.dispose()
            }
          }
        })
        scene.remove(sphere)
      })

      particles.forEach((particle) => {
        particle.geometry.dispose()
        if (Array.isArray(particle.material)) {
          particle.material.forEach((material: THREE.Material) => material.dispose())
        } else {
          particle.material.dispose()
        }
        scene.remove(particle)
      })

      renderer.dispose()
      if (container.contains(renderer.domElement)) {
        container.removeChild(renderer.domElement)
      }
    }
  }, [])

  return (
    <div className="relative w-full h-full flex flex-col items-center overflow-y-auto overflow-x-hidden">
      <div id="menu-canvas-container" className="fixed inset-0 -z-10" />
      <div className="fixed inset-0 bg-gradient-to-b from-slate-950/60 via-blue-950/50 to-slate-950/60 -z-10" />

      <div className="relative z-10 w-full max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 pt-12 sm:pt-16 lg:pt-20 pb-8 sm:pb-12 lg:pb-16">
        <header className="text-center mb-8 sm:mb-12 lg:mb-16 slide-down relative z-20">
          <div className="relative inline-block mb-6 sm:mb-8">
            <h1 className="text-4xl sm:text-6xl md:text-7xl lg:text-8xl font-black mb-2 sm:mb-4 leading-[1.1] gradient-text drop-shadow-2xl">
              H·ªá M·∫∑t Tr·ªùi
            </h1>
            <div className="absolute -top-1 -right-1 sm:-top-2 sm:-right-2 text-2xl sm:text-3xl md:text-4xl animate-bounce-gentle">‚ú®</div>
            <div className="absolute -bottom-1 -left-1 sm:-bottom-2 sm:-left-2 text-xl sm:text-2xl md:text-3xl animate-bounce-gentle" style={{ animationDelay: "0.5s" }}>‚≠ê</div>
          </div>
          <h2 className="text-xl sm:text-3xl md:text-4xl lg:text-5xl font-bold mb-3 sm:mb-4 text-blue-200 drop-shadow-xl">
            Tr√≤ Ch∆°i To√°n H·ªçc
          </h2>
          <p className="text-sm sm:text-base md:text-lg lg:text-xl text-slate-100 font-medium max-w-2xl mx-auto leading-relaxed px-4">
            Kh√°m Ph√° Quy Lu·∫≠t D√£y S·ªë C√πng C√°c H√†nh Tinh
          </p>
        </header>

        <section className="grid grid-cols-1 md:grid-cols-3 gap-6 sm:gap-8 lg:gap-10 mb-8 sm:mb-12">
          {GAMES.map((game, idx) => {
            const gameRoutes: Record<Exclude<GameType, "menu">, string> = {
              [GAME_TYPES.NEXT]: "/next-number",
              [GAME_TYPES.MISSING]: "/missing-number",
              [GAME_TYPES.SORT]: "/sort-sequence",
            }
            const guideRoutes: Record<Exclude<GameType, "menu">, string> = {
              [GAME_TYPES.NEXT]: "/next-number/guide",
              [GAME_TYPES.MISSING]: "/missing-number/guide",
              [GAME_TYPES.SORT]: "/sort-sequence/guide",
            }
            
            return (
            <div
              key={game.gameType}
              className="group relative h-[380px] sm:h-[420px] md:h-[400px] lg:h-[420px] transition-all duration-500 hover:scale-[1.05] card-hover"
              style={{ animationDelay: `${idx * 0.1}s` }}
            >
              <div
                className="absolute inset-0 rounded-2xl sm:rounded-3xl shadow-2xl group-hover:shadow-[0_20px_60px_rgba(0,0,0,0.6)] transition-all duration-500 group-hover:-translate-y-2"
                style={{
                  background: `linear-gradient(135deg, ${
                    game.gameType === GAME_TYPES.NEXT
                      ? "rgb(37, 99, 235) 0%, rgb(59, 130, 246) 50%, rgb(34, 211, 238) 100%"
                      : game.gameType === GAME_TYPES.MISSING
                        ? "rgb(249, 115, 22) 0%, rgb(244, 63, 94) 50%, rgb(236, 72, 153) 100%"
                        : "rgb(147, 51, 234) 0%, rgb(139, 92, 246) 50%, rgb(236, 72, 153) 100%"
                  })`,
                  backgroundSize: "200% 200%",
                  animation: "gradient-shift 5s ease infinite",
                }}
              />

              <div className="absolute inset-0 glass-strong rounded-2xl sm:rounded-3xl group-hover:bg-white/15 transition-all duration-500" />

              <div className="absolute inset-0 rounded-2xl sm:rounded-3xl overflow-hidden">
                <div className="absolute inset-0 bg-gradient-to-r from-transparent via-white/20 to-transparent -translate-x-full group-hover:translate-x-full transition-transform duration-1000" />
              </div>

              <div className="relative h-full flex flex-col items-center justify-center p-5 sm:p-6 md:p-7 lg:p-8 rounded-2xl sm:rounded-3xl">
                <div
                  className="text-6xl sm:text-7xl md:text-8xl lg:text-9xl mb-4 sm:mb-5 md:mb-6 bounce-gentle filter drop-shadow-2xl"
                  style={{ animationDelay: `${idx * 0.15}s` }}
                >
                  {game.emoji}
                </div>

                <h3 className="text-lg sm:text-xl md:text-2xl lg:text-3xl font-black text-white mb-2 sm:mb-3 md:mb-4 text-balance drop-shadow-2xl text-center leading-tight px-2">
                  {game.title}
                </h3>

                <p className="text-xs sm:text-sm md:text-base text-blue-50/95 text-center mb-4 sm:mb-5 md:mb-6 leading-relaxed px-2 sm:px-3 font-medium flex-grow flex items-center">
                  {game.description}
                </p>

                <div className="px-4 sm:px-5 md:px-6 py-2 sm:py-2.5 md:py-3 bg-white/30 rounded-full text-xs sm:text-sm font-bold text-white backdrop-blur-md border-2 border-white/40 shadow-xl group-hover:bg-white/40 group-hover:scale-105 transition-all duration-300">
                  {game.difficulty}
                </div>

                <div className="absolute bottom-4 sm:bottom-6 md:bottom-8 w-full flex gap-2 justify-center opacity-0 group-hover:opacity-100 transition-all duration-500 transform translate-y-2 group-hover:translate-y-0">
                  <Link
                    href={guideRoutes[game.gameType]}
                    className="btn-modern flex items-center gap-2 px-4 py-2 bg-white/20 hover:bg-white/30 text-white rounded-lg text-sm font-semibold backdrop-blur-md border border-white/30"
                    onClick={(e) => e.stopPropagation()}
                  >
                    <BookOpen size={16} />
                    <span>H∆∞·ªõng D·∫´n</span>
                  </Link>
                  <Link
                    href={gameRoutes[game.gameType]}
                    className="btn-modern flex items-center gap-2 px-4 py-2 bg-white/30 hover:bg-white/40 text-white rounded-lg text-sm font-semibold backdrop-blur-md border border-white/40"
                    onClick={(e) => e.stopPropagation()}
                  >
                    <span>Ch∆°i Ngay</span>
                  </Link>
                </div>
              </div>
            </div>
          )})}
        </section>

        <footer className="text-center slide-up" style={{ animationDelay: "0.4s" }}>
          <div className="glass-light px-5 sm:px-6 md:px-8 py-3 sm:py-4 rounded-xl sm:rounded-2xl border border-white/10 inline-block">
            <p className="text-sm sm:text-base md:text-lg lg:text-xl text-slate-100 font-semibold drop-shadow-lg">
              Ch·ªçn m·ªôt tr√≤ ch∆°i ƒë·ªÉ b·∫Øt ƒë·∫ßu h·ªçc to√°n vui v·∫ª! üöÄ
            </p>
          </div>
        </footer>
      </div>
    </div>
  )
}
